.display-card {
	border: none;
	border-radius: 4px;
	-webkit-box-shadow: 0 3px 4px #E8DDB5;
	        box-shadow: 0 3px 4px #E8DDB5;
	cursor: pointer;
	margin: 0.83%;
	max-height: 240px;
	max-width: 175px;
	min-height: 140px;
	min-width: 120px;
	width: 30%;
}

.win {
	-webkit-box-shadow: 0 1px 10px #555;
	        box-shadow: 0 1px 10px #555;
}



