.card {
	-ms-flex-align: center;
	    align-items: center;
	border-radius: 4px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

	.card-title {
		font-size: 0.9rem;
		height: 34px;
		margin: 2px;
		text-align: center;
	}

	.card-data {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		width: 100%;
	}

		.card-data-score {
			-ms-flex-item-align: center;
			    align-self: center;
			font-size: 2rem;
		}

		.card-data-info {
			font-size: 0.85rem;
			font-weight: lighter;
			margin: 2px;
			text-align: center;
		}

